<template>
	<view class="register">
		<view class="formBox">
			<view class="mititle">
				个人中心
				<u-icon name="bell-fill" style="color: #fff;position: absolute;right: 30rpx;top:30rpx" @click="notice"></u-icon>
			</view>
			<u-row class="header">
				<u-col span="3" style="text-align: center;">
					<u-avatar :src="zxlist.userFile" size="100"></u-avatar>
				</u-col>
				<u-col span="9">
					<span>您好，{{zxlist.universalAntiUserMain27}}</span>
					<p>{{zxlist.createTime}}</p>
				</u-col>
			</u-row>
			<view class="mybox">
				<u-row>
					<u-col span="6" style="text-align: center;">
						<span>注册日期</span>
						<p>{{zxlist.updateTime}}</p>
					</u-col>
					<u-col span="6" style="text-align: center;border-left: 1px solid #E1E1E2;">
						<span>已获积分</span>
						<p>A:{{zxlist.scoreA}} | B:{{zxlist.scoreB}}</p>
					</u-col>
				</u-row>
			</view>
			<u-cell-group class="tsbox">
				<u-cell-item title="我创建的活动" :value="'('+cjlist.length+')'" @click="topage(0)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="../../static/img/my_icon1.png"
						mode="widthFix"></img>
				</u-cell-item>
				<u-cell-item title="我参与的活动" :value="'('+cylist.length+')'" @click="topage(1)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="../../static/img/my_icon2.png"
						mode="widthFix"></img>
				</u-cell-item>
				<u-cell-item title="我的待办" @click="topage(2)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="../../static/img/my_icon3.png"
						mode="widthFix"></img>
				</u-cell-item>
				<u-cell-item title="我的线索" @click="topage(3)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="../../static/img/my_icon4.png"
						mode="widthFix"></img>
				</u-cell-item>
				<u-cell-item title="我的动态" @click="topage(4)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="../../static/img/my_icon5.png"
						mode="widthFix"></img>
				</u-cell-item>
				<u-cell-item title="退出会员名单" @click="topage(5)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="../../static/img/my_icon6.png"
						mode="widthFix"></img>
				</u-cell-item>
				<u-cell-item title="我的物品" @click="topage(6)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="../../static/img/my_icon7.png"
						mode="widthFix"></img>
				</u-cell-item>
				<u-cell-item title="设置" @click="topage(8)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="../../static/img/my_icon8.png"
						mode="widthFix"></img>
				</u-cell-item>
			</u-cell-group>
			<u-gap height="30" bg-color="#F5F6FA" style="margin: 0 -30rpx;"></u-gap>
			<u-button @click="exit">退出登录</u-button>
			<u-gap height="100" bg-color="#F5F6FA" style="margin: 0 -30rpx;"></u-gap>
		</view>
		
			<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		getUserInfo,
		enterAbout,
		trendsObj
	} from '../../api/index.js'
	export default {
		data() {
			return {
				csList: {
					activityUserId: null,
					objectId: 298
				},
				zxlist: {},
				cylist: [],
				cjlist: [],
			}
		},
		onLoad() {
			getUserInfo().then(res => {
				if (res.code == 200) {
					this.zxlist = res.baseInfo;
					this.csList.activityUserId = this.zxlist.userId;
					trendsObj(this.csList).then(res => {
						if (res.code == 200) {
							this.cjlist = res.rows;
						}
					});
				}
			});
			enterAbout({
				id: this.zxlist.id
			}).then(res => {
				if (res.code == 200) {
					this.cylist = res.data;
				}
			});

		},
		methods: {
			notice(){
				uni.navigateTo({
					url: 'notice'
				})
			},
			exit(){
				/*enterAbout({
					id: this.zxlist.id
				}).then(res => {
					if (res.code == 200) {*/
						this.$refs.uToast.show({
							title: '退出成功'
						});
						uni.setStorageSync('token', '');
						uni.navigateTo({
							url: '../login/login'
						})
					//}
				//});
			},
			topage(index) {
				let link = '';
				if (index == 0) {
					link = 'initiate';
				} else if (index == 1) {
					link = 'participation';
				} else if (index == 2) {
					link = 'dealt';
				} else if (index == 3) {
					link = 'clue';
				} else if (index == 4) {
					link = 'state';
				} else if (index == 5) {
					link = 'exit';
				} else if (index == 6) {
					link = 'things';
				} else if (index == 7) {
					link = '../news/list';
				}else if (index == 8) {
					link = 'set';
				}
				uni.navigateTo({
					url: link
				})
			},
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		width: 100vw;
		height: 100vh;
	}

	.register /deep/ .u-btn {
		border: none;
		border-radius: 0;
		position: initial;
	}

	.header {
		margin-top: 42rpx;
		margin-bottom: -60rpx;
	}

	.header span {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}

	.header p {
		margin-top: 12rpx;
		font-size: 24rpx;
		color: #999999;
	}

	.mybox {
		height: 342rpx;
		background: url('@/static/img/bar.png') no-repeat center center;
		background-size: cover;
		box-sizing: border-box;
		margin: 0 -30rpx;
		padding-top: 105rpx;
		color: #fff;
	}

	.mybox span {
		font-size: 24rpx;
	}

	.mybox p {
		font-size: 28rpx;
		font-weight: bold;
		margin-top: 10rpx;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		background-color: #1F61BE;
		text-align: center;
		font-size: 32rpx;
		font-weight: bold;
		color: #fff;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		position: relative;
	}

	.tsbox {
		margin-top: -75rpx;
	}
    .tsbox /deep/ .u-cell-item-box{
		background-color: transparent;
	}
	.tsbox /deep/ .u-cell_title {
		font-size: 24rpx;
		color: #333;
	}

	.tsbox /deep/ .u-cell__value {
		font-size: 24rpx;
		color: #4490FB;
	}
</style>